import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const browseClassForBonusEstimation = paramSearch =>
  request({
    prefix,
    url: '/BonusNote/BrowseClassForBonusEstimation',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });

export const browseBonusEstimationClass = paramSearch =>
  request({
    prefix,
    url: '/BonusNote/BrowseBonusEstimationClass',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });

export const createClassBonusEstimation = async data =>
  request({
    prefix,
    url: '/BonusNote/CreateClassBonusEstimation',
    method: 'POST',
    params: {},
    data,
  });

export const deleteClassBonusEstimation = async data =>
  request({
    prefix,
    url: '/BonusNote/DeleteClassBonusEstimation',
    method: 'DELETE',
    params: {},
    data,
  });

export const exportClassBonusEstimation = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
} = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'Danh sch khen thng tp th.xlsx',
    url: '/BonusNote/ExportClassBonusEstimation',
    method: 'POST',
    params: {},
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId, semesterId, yearEduId },
  });



