import React from 'react';
import { browseClassForBonusEstimation } from 'services/considerEmulationAndReward';
import { TableData } from 'components/common';

const columns = [
  {
    title: 'Lp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'S s',
    dataIndex: 'noOfStudent',
    key: 'noOfStudent',
  },
  {
    title: 'HS kh',
    dataIndex: 'noOfGreat',
    key: 'noOfGreat',
  },
  {
    title: 'HS gii',
    dataIndex: 'noOfGood',
    key: 'noOfGood',
  },
  {
    title: 'HS xut sc',
    dataIndex: 'noOfExcellent',
    key: 'noOfExcellent',
  },
  {
    title: 'Danh hiu',
    dataIndex: 'gradeTitle',
    key: 'gradeTitle',
  },
];

const TableClasses = props => {
  const rowSelection = {
    selectedRowKeys: props.currentSelectedClasses.map(selectedClasses => selectedClasses.classId),
    onChange: (selectedRowKeys, selectedRows) => {
      props.setCurrentSelectedClasses(selectedRows);
    },
  };

  return (
    <TableData
      reload={props.reload}
      rowSelection={rowSelection}
      service={browseClassForBonusEstimation}
      paramSearch={props.paramSearch}
      columns={columns}
      allowGetData={!!props.paramSearch.semesterId && !!props.paramSearch.yearEduId}
      rowKey="classId"
    />
  );
};

export default TableClasses;



