import React, { useEffect } from 'react';
import { Form, Row, Col, Checkbox } from 'antd';
import { BtnSearch } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { YearSemesterInput } from 'components/common';

const FormSearch = props => {
  useEffect(() => {
    const params = getQueryParams();
    delete params._time;
    params.includeRetest = params.includeRetest === 'true';
    props.form.setFieldsValue(params);
  }, []);

  const handleSearch = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };

  const optionFieldDecorator = {
    semesterId: {
      rules: [
        {
          required: true,
          message: 'Vui lng chn hc k!',
        },
      ],
    },
    yearEduId: {
      rules: [
        {
          required: true,
          message: 'Vui lng chn nm hc!',
        },
      ],
    },
  };

  return (
    <Form className="form-inline-label" layout="vertical" onSubmit={handleSearch}>
      <Row gutter={24}>
        <FilterPathEducate form={props.form} />
        <YearSemesterInput form={props.form} span={5} optionFieldDecorator={optionFieldDecorator} />
        <Col span={5}>
          <Form.Item required>
            {props.form.getFieldDecorator('includeRetest', {
              valuePropName: 'checked',
            })(<Checkbox>Tnh c im hc li</Checkbox>)}
          </Form.Item>
        </Col>
        <Col span={5}>
          <div className="btn-group">
            <BtnSearch htmlType="submit" />
          </div>
        </Col>
      </Row>
    </Form>
  );
};

export default Form.create()(FormSearch);



